import React, { useState, useEffect } from "react";
import InputBar from "./InputBar";
import WordList from "./WordList";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Modal from "react-bootstrap/Modal";

const App = () => {
  const [wordList, setWordList] = useState([]);
  const [inactiveWords, setInactiveWords] = useState([]);
  const [simScores, setSimScores] = useState([]);
  const [isStarted, setIsStarted] = useState(false);
  const [show, setShow] = useState(false);

  const handleClose = () => {
    setShow(false);
    reset();
  };
  const handleOpen = () => setShow(true);

  useEffect(() => {
    if (wordList.length === 1 && isStarted) {
      handleOpen();
    }
    if (wordList.length === 0 && isStarted) {
      alert("There doesn't seem to be a solution to this problem");
    }
  }, [wordList, isStarted]);

  const checkForDuplicates = (word) => {
    return !wordList.includes(word);
  };

  const checkLength = (word) => {
    return wordList[0].length === word.length;
  };

  const addToList = (word) => {
    if (word.length > 3) {
      if (wordList.length < 1) {
        setWordList([...wordList, word]);
      } else {
        if (checkLength(word) && checkForDuplicates(word)) {
          setWordList([...wordList, word]);
        } else {
          alert(
            checkLength(word)
              ? "word is already in list"
              : "word is not the same length as the others"
          );
        }
      }
    } else {
      alert("your word is too short");
    }
  };

  const setupSimMatrix = (list) => {
    let size = list.length;
    let scores = new Array(size);
    for (var i = 0; i < size; i++) {
      scores[i] = new Array(size);
      for (var j = 0; j < size; j++) {
        scores[i][j] = -1;
      }
    }
    return scores;
  };

  const calcSim = (list) => {
    let scores = setupSimMatrix(list);
    let size = scores.length;
    for (var i = 0; i < size; i++) {
      for (var j = 0; j < size; j++) {
        if (i !== j) {
          scores[i][j] = scores[j][i] = checkSimilarity(list[i], list[j]);
        }
      }
    }
    return scores;
  };

  const startProgram = (list) => {
    let scores = calcSim(list);
    setSimScores(scores);
    setIsStarted(true);
  };

  const checkSimilarity = (a, b) => {
    let sim = 0;
    for (let i = 0; i < a.length; i++) {
      if (a[i] === b[i]) {
        sim++;
      }
    }
    return sim;
  };

  const removeWord = (word) => {
    let newlist = wordList.filter((w) => {
      return w !== word;
    });
    setWordList(newlist);
  };

  const chooseWord = (word, sim) => {
    let idx = wordList.indexOf(word);
    //calculate all words to exclude
    let matches = [];
    //go through the sim scores for that word
    //each score that matches the sim number is added to matches
    simScores[idx].forEach((element, index) => {
      if (element === sim) {
        matches.push(index);
      }
    });
    let keep = wordList.filter((word, index) => {
      return matches.includes(index);
    });
    let chuck = wordList.filter((word) => {
      return !keep.includes(word);
    });
    setWordList(keep);
    setInactiveWords([...inactiveWords, ...chuck]);
    //all matches are removed from wordList
    setSimScores(calcSim(keep));
  };

  const reset = () => {
    setWordList([]);
    setSimScores([]);
    setInactiveWords([]);
    setIsStarted(false);
  };

  const Input = () => {
    return !isStarted ? (
      <InputBar
        onSubmit={(word) => addToList(word)}
        handleStart={() => startProgram(wordList)}
        handleReset={() => reset()}
      />
    ) : (
      <Button variant="danger" type="submit" onClick={() => reset()}>
        Reset
      </Button>
    );
  };

  return (
    <Container>
      <Input />
      <WordList
        list={wordList}
        chooseWord={chooseWord}
        inactiveList={inactiveWords}
        simScores={simScores}
        handleRemove={removeWord}
        isStarted={isStarted}
      />
      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header>Your word is:</Modal.Header>
        <Modal.Body>{wordList[0]}</Modal.Body>
        <Modal.Footer>
          <Button onClick={handleClose}>Reset</Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default App;
