import React from "react";
import { Button } from "react-bootstrap";

const WordCard = (props) => {
  const displaySims = () => {
    let key = 0;
    const scores = [...new Set(props.simScores)].sort();
    return scores.map((sim) => {
      if (sim > -1) {
        return (
          <button key={key++} onClick={() => props.chooseWord(props.word, sim)}>
            {sim}
          </button>
        );
      }
    });
  };

  const displayRemove = (props) => {
    return (
      <Button onClick={() => props.handleRemove(props.word)}>Remove</Button>
    );
  };

  return (
    <div>
      {props.word}
      {!props.isStarted ? displayRemove(props) : null}
      {!props.inactive ? displaySims() : null}
    </div>
  );
};

export default WordCard;
