import React from "react";
import WordCard from "./WordCard";
import ListGroup from "react-bootstrap/ListGroup";

const WordList = (props) => {
  if (!props.list) {
    return <div></div>;
  }

  let content, inactiveContent;

  content = props.list.map((word, index) => {
    return (
      <ListGroup.Item key={index}>
        <WordCard
          index={props.list.indexOf(word)}
          chooseWord={props.chooseWord}
          word={word}
          simScores={props.simScores[index]}
          handleRemove={props.handleRemove}
          isStarted={props.isStarted}
        />
      </ListGroup.Item>
    );
  });

  inactiveContent = props.inactiveList.map((word, index) => {
    return (
      <ListGroup.Item disabled key={index}>
        <WordCard
          inactive
          word={word}
          simScores={props.simScores[props.list.indexOf(word)]}
        />
      </ListGroup.Item>
    );
  });

  return (
    <div>
      <div>{content}</div>
      <div>{inactiveContent}</div>
    </div>
  );
};

export default WordList;
